// import { Icon } from '@iconify/react';
import { useRef, useState, useEffect } from 'react';
// import attach2Fill from '@iconify/icons-eva/attach-2-fill';
// import roundAddPhotoAlternate from '@iconify/icons-ic/round-add-photo-alternate';
// material
import {
  Box,
  Button,
  TextField,
  // IconButton
} from '@mui/material';
import { message } from 'antd'
import axios from "axios"
import jwt_decode from 'jwt-decode';
// Redux
import { useDispatch } from '../../../redux/store';
import { updateReply } from '../../../redux/slices/mail';


// ----------------------------------------------------------------------

export default function MailDetailsReplyInput(props) {
  const fileInputRef = useRef(null);
  const [email, setMessage] = useState('');
  const [accept, setAccept] = useState('');
  const myEMail = jwt_decode(localStorage.getItem('token')).email;
  // const myUsername = jwt_decode(localStorage.getItem('token')).name;
  const dispatch = useDispatch();

  useEffect(() => {
    setAccept(props.mail.accept)
  }, []);

  message.config({ top: window.innerHeight - 200, duration: 5 });

  const handleChangeMessage = (event) => {
    setMessage(event.target.value);
  };

  // const handleAttach = () => {
  //   fileInputRef.current.click();
  // };

  const sendEmail = () => {
    if (!email) {
      message.error(`You must type message.`);
      return
    }
    else {
      var sending = {
        id: props.emailID,
        senderName: jwt_decode(localStorage.getItem('token')).name,
        receiver: props.mail.from.email,
        subject: `RE: ${props.mail.subject}`,
        inReplyTo: props.mail.messageId,
        message: email,
        email: myEMail
      }

      // Send Email
      axios.post(`${process.env.REACT_APP_SERVER_URL}/sendEmail`, sending)
        .then((res) => {
          if (res.data.flag === "success") {
            message.success(`Message sent.`);
            setMessage('');
            dispatch(updateReply());
          }
          else if (res.data.flag === 'Error occured.') {
            message.error(res.data.message);
          }
        })
        .catch((err) => {
          message.error(err);
        });
    }
  }

  const acceptEmail = () => {
    axios.post(`${process.env.REACT_APP_SERVER_URL}/acceptEmailById`, {
      id: props.emailID,
      staff: myEMail
    })
      .then((res) => {
        if (res.data.flag === "success") {
          setAccept(myEMail)
          message.info("This email is moved to your private Inbox.")
        }
        else if (res.data.flag === "accepted") {
          setAccept(res.data.data)
          message.error(`This email already accepted by ${res.data.data}`);
        }
      })
  }

  const rejectEmail = () => {
    axios.post(`${process.env.REACT_APP_SERVER_URL}/rejectEmailById`, {
      id: props.emailID,
    })
      .then((res) => {
        if (res.data.flag === "success") {
          setAccept("")
          message.info("This email is moved to General Inbox.")
        }
      })
  }

  return (
    <>
      {accept && accept === myEMail &&
        <TextField
          fullWidth
          multiline
          minRows={2}
          maxRows={8}
          value={email}
          placeholder="Type a message"
          onChange={handleChangeMessage}
          sx={{ '& fieldset': { border: 'none !important' } }}
        />
      }

      {(!accept || accept !== myEMail) &&
        <TextField
          disabled
          fullWidth
          multiline
          minRows={2}
          maxRows={8}
          value={email}
          placeholder="Type a message"
          onChange={handleChangeMessage}
          sx={{ '& fieldset': { border: 'none !important' } }}
        />
      }

      <Box
        sx={{
          mr: 3,
          mb: 3,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-end'
        }}
      >
        {/* <IconButton size="small" onClick={handleAttach}>
          <Icon icon={roundAddPhotoAlternate} width={24} height={24} />
        </IconButton>

        <IconButton size="small" onClick={handleAttach} sx={{ ml: 1, mr: 2 }}>
          <Icon icon={attach2Fill} width={24} height={24} />
        </IconButton> */}
        {!accept &&
          <Button onClick={acceptEmail} variant="contained" color="success" sx={{ ml: 1, mr: 2 }}>Accept</Button>
        }
        {accept &&
          <>
            {accept === myEMail &&
              <>
                <Button onClick={rejectEmail} variant="contained" color="warning" sx={{ ml: 1, mr: 2 }}>Reject</Button>
                <Button onClick={sendEmail} variant="contained">Send</Button>
              </>
            }
          </>
        }
      </Box>

      <input type="file" ref={fileInputRef} style={{ display: 'none' }} />
    </>
  );
}
