import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
// material
import { styled } from '@mui/material/styles';
import { Box, Divider, Typography, Link } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../../redux/store';
import { getMail } from '../../../redux/slices/mail';
//
import Markdown from '../../Markdown';
// import Scrollbar from '../../Scrollbar';
import MailDetailsToolbar from './MailDetailsToolbar';
import MailDetailsReplyInput from './MailDetailsReplyInput';
// import MailDetailsAttachments from './MailDetailsAttachments';
import { fDateTimeSuffix } from '../../../utils/formatTime';
import axios from 'axios'
// utils
import createAvatar from '../../../utils/createAvatar';
//
import { MAvatar } from '../../@material-extend';

// ----------------------------------------------------------------------

const RootStyle = styled('div')({
  flexGrow: 1,
  display: 'flex',
  flexDirection: 'column'
});

const MarkdownWrapperStyle = styled('div')(({ theme }) => ({
  '& > p': {
    ...theme.typography.body1,
    marginBottom: theme.spacing(2)
  }
}));

// ----------------------------------------------------------------------

export default function MailDetails() {
  const [reply, setReply] = useState([])
  const { mailId } = useParams();
  const dispatch = useDispatch();
  const mail = useSelector((state) => state.mail.mails.byId[mailId]);
  const updateReply = useSelector((state) => state.mail.updateReply);
  // const isAttached = mail && mail.files.length > 0;

  useEffect(() => {
    dispatch(getMail(mailId));
    axios.post(`${process.env.REACT_APP_SERVER_URL}/getRepliedEmailById`, { id: mailId })
      .then((res) => {
        setReply(res.data.result)
        console.log(res.data.result)
      })
  }, [dispatch, mailId, updateReply]);

  if (!mail) {
    return null;
  }

  const showReply = (value) => {
    if (value.category === 0) {
      return (
        <>
          {/* <Box sx={{ p: { xs: 3, md: 5 }, display: 'flex' }} {mailId === value.id ? "111" : "000"}> */}
          <Box sx={{
            p: { xs: 3, md: 5 },
            display: 'flex',
            ...(mailId === value.id && { bgcolor: 'red' })
          }}>

            <MAvatar sx={{ mr: '15px' }} alt={mail.from.name} src={mail.from.avatar} color={createAvatar(value.fromName).color}>
              {createAvatar(value.fromName).name}
            </MAvatar>
            <Box sx={{ flex: '1 1 0%' }}>
              <MarkdownWrapperStyle>
                <Markdown children={value.message} />
              </MarkdownWrapperStyle>
              <Box sx={{ display: 'flex' }}>

                <Typography variant="caption" sx={{ color: 'text.secondary', display: 'block', flex: '1 1 0%' }}>
                  <Link color="inherit" key={value.fromEmail}>
                    {value.fromEmail}
                  </Link>
                </Typography>
                <Typography variant="caption" sx={{ color: 'text.secondary', flex: '1 1 0%', textAlign: 'right' }}>
                  {fDateTimeSuffix(value.createdAt)}
                </Typography>
              </Box>
            </Box>
          </Box>
          <Divider />
        </>
      )
    }
    else if (value.category === 1) {
      return (
        <>
          <Box sx={{
            p: { xs: 3, md: 5 },
            display: 'flex',
            ...(mailId === value.id && { bgcolor: 'red' })
          }}>
            <MAvatar sx={{ mr: '15px' }} alt={mail.from.name} src={mail.from.avatar} color={createAvatar(value.fromName).color}>
              {createAvatar(value.fromName).name}
            </MAvatar>
            <Box sx={{ flex: '1 1 0%' }}>
              <MarkdownWrapperStyle>
                <Markdown sx={{ whiteSpace: 'pre-line' }} children={value.message} />
              </MarkdownWrapperStyle>

              <Box sx={{ display: 'flex' }}>
                <Typography variant="caption" sx={{ color: 'text.secondary', display: 'block', flex: '1 1 0%' }}>
                  <Link color="inherit" key={value.accept}>
                    {value.username}
                  </Link>
                </Typography>
                <Typography variant="caption" sx={{ color: 'text.secondary', flex: '1 1 0%', textAlign: 'right' }}>
                  {fDateTimeSuffix(value.createdAt)}
                </Typography>
              </Box>
            </Box>
          </Box>
          <Divider />
        </>
      )
    }
  }

  return (
    <RootStyle>
      {mail !== undefined && mail && (
        <>
          <MailDetailsToolbar mail={mail} />
          <Divider />

          <Box sx={{ minHeight: '65vh' }}>
            <Box sx={{ px: { xs: 3, md: 5 }, pt: { xs: 3, md: 5 } }}>
              <Typography variant="h3" gutterBottom>
                {reply[0] && reply[0].subject}
              </Typography>
            </Box>

            {reply && reply.map((value) => (
              showReply(value)
            ))}
          </Box>

          <Divider />

          <MailDetailsReplyInput emailID={mailId} mail={mail} />
        </>
      )}
    </RootStyle>
  );
}
