import { useState } from 'react';
import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import closeFill from '@iconify/icons-eva/close-fill';
import expandFill from '@iconify/icons-eva/expand-fill';
// import attach2Fill from '@iconify/icons-eva/attach-2-fill';
import collapseFill from '@iconify/icons-eva/collapse-fill';
import { useDispatch } from '../../../redux/store';
import { settoggleCompose } from '../../../redux/slices/mail';
// import roundAddPhotoAlternate from '@iconify/icons-ic/round-add-photo-alternate';
// material
import { useTheme, styled } from '@mui/material/styles';
import { Box, Input, Portal, Button, Divider, Backdrop, IconButton, Typography, useMediaQuery } from '@mui/material';
//
import { QuillEditor } from '../../editor';
import axios from 'axios'
import { message } from "antd"
import jwt_decode from 'jwt-decode';

// ----------------------------------------------------------------------

const RootStyle = styled('div')(({ theme }) => ({
  right: 0,
  bottom: 0,
  zIndex: 1999,
  minHeight: 440,
  outline: 'none',
  display: 'flex',
  position: 'fixed',
  overflow: 'hidden',
  flexDirection: 'column',
  margin: theme.spacing(3),
  boxShadow: theme.customShadows.z20,
  borderRadius: theme.shape.borderRadiusMd,
  backgroundColor: theme.palette.background.paper
}));

const InputStyle = styled(Input)(({ theme }) => ({
  padding: theme.spacing(0.5, 3),
  borderBottom: `solid 1px ${theme.palette.divider}`
}));

// ----------------------------------------------------------------------

MailCompose.propTypes = {
  isOpenCompose: PropTypes.bool,
  onCloseCompose: PropTypes.func
};

export default function MailCompose({ isOpenCompose, onCloseCompose }) {
  const theme = useTheme();
  const dispatch = useDispatch();
  // var composeStatue = useSelector((state) => state.mail.toggleCompose);
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [fullScreen, setFullScreen] = useState(false);
  const [html, setMessage] = useState('');
  const [receiver, setReceiver] = useState('');
  const [subject, setSubject] = useState('');
  const myEMail = jwt_decode(localStorage.getItem('token')).email;

  const handleChangeMessage = (value) => {
    setMessage(value);
  };

  const handleExitFullScreen = () => {
    setFullScreen(false);
  };

  const handleEnterFullScreen = () => {
    setFullScreen(true);
  };

  const handleClose = () => {
    dispatch(settoggleCompose(false));
    onCloseCompose();
    setFullScreen(false);
  };

  var sending = {
    id: '',
    receiver: receiver,
    senderName: jwt_decode(localStorage.getItem('token')).name,
    subject: subject,
    inReplyTo: '',
    message: html,
    email: myEMail
  }

  const messageSend = () => {
    axios.post(`${process.env.REACT_APP_SERVER_URL}/sendEmail0`, sending)
      .then((res) => {
        console.log(res.data)
        message.config({ top: window.innerHeight - 200, duration: 5 });
        if (res.data.flag === "success") {
          message.success(`Message sent.`);
          setMessage("")
          setReceiver("")
          setSubject("")
        }
        else if (res.data.flag === 'Error occured.') {
          message.error(res.data.message);
        }
      })
      .catch((err) => {
        message.error(err);
      });
  }

  if (!isOpenCompose) {
    return null;
  }

  return (
    <Portal >
      <Backdrop open={fullScreen || isMobile} sx={{ zIndex: 1998 }} />
      <RootStyle
        sx={{
          ...(fullScreen && {
            top: 0,
            left: 0,
            zIndex: 1999,
            margin: 'auto',
            width: {
              xs: `calc(100% - 24px)`,
              md: `calc(100% - 80px)`
            },
            height: {
              xs: `calc(100% - 24px)`,
              md: `calc(100% - 80px)`
            }
          }),
          maxHeight: '95vh',
        }}
      >
        <Box
          sx={{
            pl: 3,
            pr: 1,
            height: 60,
            display: 'flex',
            alignItems: 'center'
          }}
        >
          <Typography variant="h6">New Message</Typography>
          <Box sx={{ flexGrow: 1 }} />

          <IconButton onClick={fullScreen ? handleExitFullScreen : handleEnterFullScreen}>
            <Icon icon={fullScreen ? collapseFill : expandFill} width={20} height={20} />
          </IconButton>

          <IconButton onClick={handleClose}>
            <Icon icon={closeFill} width={20} height={20} />
          </IconButton>
        </Box>

        <Divider />

        <InputStyle value={receiver} onChange={(e) => setReceiver(e.target.value)} disableUnderline placeholder="To" />
        <InputStyle value={subject} onChange={(e) => setSubject(e.target.value)} disableUnderline placeholder="Subject" />

        <QuillEditor
          simple
          id="compose-mail"
          value={html}
          onChange={handleChangeMessage}
          placeholder="Type a message"
          sx={{
            overflowY: 'scroll',
            borderColor: 'transparent',
            flexGrow: 1
          }}
        />

        <Divider />

        <Box sx={{ py: 2, px: 3, display: 'flex', alignItems: 'center' }}>
          <Button variant="contained" onClick={messageSend}>Send</Button>

          {/* <IconButton size="small" sx={{ ml: 2, mr: 1 }}>
            <Icon icon={roundAddPhotoAlternate} width={24} height={24} />
          </IconButton>

          <IconButton size="small">
            <Icon icon={attach2Fill} width={24} height={24} />
          </IconButton> */}
        </Box>
      </RootStyle>
    </Portal>
  );
}
