import PropTypes from 'prop-types';
// import { useParams } from 'react-router-dom';
import { Icon } from '@iconify/react';
// import menuFill from '@iconify/icons-eva/menu-fill';
import searchFill from '@iconify/icons-eva/search-fill';
import refreshFill from '@iconify/icons-eva/refresh-fill';
import collapseFill from '@iconify/icons-eva/collapse-fill';
import plusFill from '@iconify/icons-eva/plus-fill';
// import moreVerticalFill from '@iconify/icons-eva/more-vertical-fill';
import arrowIosBackFill from '@iconify/icons-eva/arrow-ios-back-fill';
import arrowIosForwardFill from '@iconify/icons-eva/arrow-ios-forward-fill';
// redux
import { useDispatch, useSelector } from '../../../redux/store';
import { getpage, setsearch1, settoggleCompose } from '../../../redux/slices/mail';
// material
import { styled } from '@mui/material/styles';
import {
  Box,
  Button,
  Tooltip,
  Checkbox,
  Typography,
  IconButton,
  FormControl,
  OutlinedInput,
  InputAdornment,
} from '@mui/material';
//
import { MHidden } from '../../@material-extend';
import { useState } from 'react';

// ----------------------------------------------------------------------

const RootStyle = styled('div')(({ theme }) => ({
  height: 84,
  flexShrink: 0,
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 2)
}));

const SearchStyle = styled(OutlinedInput)(({ theme }) => ({
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter
  }),
  '&.Mui-focused': { boxShadow: theme.customShadows.z8 },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${theme.palette.grey[500_32]} !important`
  },
  [theme.breakpoints.up('md')]: {
    width: 240,
    flexDirection: 'row',
    '&.Mui-focused': { width: 280 }
  }
}));

// ----------------------------------------------------------------------

MailToolbar.propTypes = {
  mails: PropTypes.number.isRequired,
  selectedMails: PropTypes.number.isRequired,
  onOpenSidebar: PropTypes.func,
  onToggleDense: PropTypes.func,
  onSelectAll: PropTypes.func,
  onDeselectAll: PropTypes.func
};

export default function MailToolbar({
  mails,
  selectedMails,
  onOpenSidebar,
  onToggleDense,
  onSelectAll,
  onDeselectAll,
  ...other
}) {
  const handleSelectChange = (event) => (event.target.checked ? onSelectAll() : onDeselectAll());

  var composeStatue = useSelector((state) => state.mail.toggleCompose);
  const selectedAllMails = selectedMails === mails && mails > 0;
  const selectedSomeMails = selectedMails > 0 && selectedMails < mails;
  const [search, setSearch] = useState('');
  const dispatch = useDispatch();
  console.log('composeStatue', composeStatue)

  var pageInfo = {
    page: useSelector((state) => state.mail.page),
    perpage: useSelector((state) => state.mail.pesrpage)
  }


  const previous = () => {
    if (pageInfo.page > 1) {
      var temp = pageInfo;
      temp.page = temp.page - 1;
      dispatch(getpage(temp))
    }
  }

  const next = () => {
    console.log('mails', mails)
    var countPage = 0;
    if (mails % pageInfo.perpage > 0) {
      countPage = Math.floor(mails / pageInfo.perpage) + 1;
    } else {
      countPage = Math.floor(mails / pageInfo.perpage)
    }

    if (pageInfo.page < countPage) {
      console.log('countPage', countPage)
      var temp = pageInfo;
      temp.page = temp.page + 1;
      dispatch(getpage(temp))
    }
  }

  function handleKeyDown(event) {
    if (event.key === "Enter") {
      console.log(search);
      dispatch(setsearch1(search))
    }
  }

  const handleCompose = () => {
    dispatch(settoggleCompose(!composeStatue))
  }

  return (
    <RootStyle {...other}>
      {/* <MHidden width="mdUp">
        <IconButton onClick={onOpenSidebar}>
          <Icon icon={menuFill} />
        </IconButton>
      </MHidden> */}

      <MHidden width="smDown">
        <Checkbox checked={selectedAllMails} indeterminate={selectedSomeMails} onChange={handleSelectChange} />
        <Tooltip title="Refresh">
          <IconButton>
            <Icon icon={refreshFill} width={20} height={20} />
          </IconButton>
        </Tooltip>
        <Tooltip title="Dense">
          <IconButton onClick={onToggleDense}>
            <Icon icon={collapseFill} width={20} height={20} />
          </IconButton>
        </Tooltip>

        {/* <Tooltip title="More">
          <IconButton>
            <Icon icon={moreVerticalFill} width={20} height={20} />
          </IconButton>
        </Tooltip> */}
      </MHidden>
      <Box sx={{ p: 3 }}>
        <Button fullWidth variant="contained" startIcon={<Icon icon={plusFill} />} onClick={handleCompose}>
          Compose
        </Button>
      </Box>
      <Box sx={{ flexGrow: 1 }} />

      <FormControl size="small">
        <SearchStyle
          placeholder="Search mail…"
          value={search}
          onChange={(event) => setSearch(event.target.value)}
          onKeyDown={handleKeyDown}
          startAdornment={
            <InputAdornment position="start">
              <Box component={Icon} icon={searchFill} sx={{ color: 'text.disabled' }} />
            </InputAdornment>
          }
        />
      </FormControl>

      <MHidden width="smDown">
        <Box sx={{ display: 'flex', alignItems: 'center', flexShrink: 0 }}>
          <Typography variant="body2" sx={{ mx: 2, color: 'text.secondary' }}>
            {((pageInfo.page - 1) * pageInfo.perpage) + 1} - {pageInfo.perpage * pageInfo.page} of {mails}
          </Typography>
          <Tooltip title="Previous page">
            <IconButton onClick={previous}>
              <Icon icon={arrowIosBackFill} width={20} height={20} />
            </IconButton>
          </Tooltip>
          <Tooltip title="Next page">
            <IconButton onClick={next}>
              <Icon icon={arrowIosForwardFill} width={20} height={20} />
            </IconButton>
          </Tooltip>
        </Box>
      </MHidden>
    </RootStyle>
  );
}
