import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
// material
import { Card } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getLabels } from '../../redux/slices/mail';
// components
import Page from '../../components/Page';
import { MailList, MailDetails, MailCompose } from '../../components/_dashboard/mail';

// ----------------------------------------------------------------------

export default function Mail() {
  // const { themeStretch } = useSettings();
  var composeStatue = useSelector((state) => state.mail.toggleCompose);
  const dispatch = useDispatch();
  const { mailId } = useParams();
  // const [openSidebar, setOpenSidebar] = useState(false);
  const [openCompose, setOpenCompose] = useState(false);

  useEffect(() => {
    dispatch(getLabels());
    setOpenCompose(composeStatue)
  }, [dispatch, composeStatue]);

  return (
    <Page title="Mail">
      {/* <Container maxWidth={themeStretch ? false : 'xl'}> */}
      <Card sx={{ minHeight: { md: '85vh' }, display: { md: 'flex' } }}>
        {/* <MailSidebar
          isOpenSidebar={openSidebar}
          onCloseSidebar={() => setOpenSidebar(false)}
          onOpenCompose={() => setOpenCompose(true)}
        /> */}
        {mailId ? <MailDetails /> : <MailList />}
        <MailCompose isOpenCompose={openCompose} onCloseCompose={() => setOpenCompose(false)} />
      </Card>
      {/* </Container> */}
    </Page>
  );
}
